//**************************************************
// HELPERS CLASSES
//**************************************************
.relative{
    position: relative;
}

.block {
    display: block;
}

.inline-block {
    display: inline-block;
}

.text-justify {
    text-align: justify;
}

.uppercase {
    text-transform: uppercase;
}

.clearfix {
    @include clearfix();
}

.rspimg {
    display: inline-block;
    max-width: 100%;
    height: auto;
    vertical-align: bottom;
}

.responsive-map {
	position: relative;
	width: 100%;
	height: 0;
	padding-bottom: 32%;

    @include max-screen($md - 1) {
        padding-bottom: 50%;
    }
	
	iframe, .map {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
	
}

.responsive-video {
    position: relative;
    padding-bottom: 56.25%;
    padding-top: 30px;
    width: 100%;
    height: 0;
    min-height: 0;
    overflow: hidden;

    iframe, object, embed, video {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}

.responsive-table {
    position: relative;
    width: 100%;
    overflow-x: auto;
    overflow-y: visible;
}

.background {
    &-holder {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        @include transition();

        img {
            max-width: 100%;
        }
    }
}

.overflow-hidden {
    overflow: hidden;
}

.no-margin {
    margin: 0 !important;
}

.no-padding {
    padding: 0 !important;
}


%list {
    margin: 0;
    padding: 0;
    list-style: none;
}


.slick-slider * { outline: none; }



// MARGINS & PADDINGS
$helper_classes: (
    properties: (m, p),

    variants: (

        t: ( 1 0 0 0 ),
        r: ( 0 1 0 0 ),
        b: ( 0 0 1 0 ),
        l: ( 0 0 0 1 ),
        v: ( 1 0 1 0 ),
        h: ( 0 1 0 1 )
    ),

    values: (0, 5, 10, 15, 20, 30, 40, 50)
);

$types: map-get($helper_classes, properties);
$variants: map-get($helper_classes, variants);
$values: map-get($helper_classes, values);

@each $propertie in $types {
    @each $variant, $props in $variants {
        @each $value in $values {

            .#{$propertie}#{$variant}#{$value} {

                $top: nth($props, 1);
                @if ($top == 1) {
                    @if ($propertie == m){
                        margin-top: $top * $value + px !important;
                    } @else {
                        padding-top: $top * $value + px !important;
                    }
                }


                $right: nth($props, 2);
                @if ($right == 1) {
                    @if ($propertie == m){
                        margin-right: $right * $value + px !important;
                    } @else {
                        padding-right: $right * $value + px !important;
                    }
                }


                $bottom: nth($props, 3);
                @if ($bottom == 1) {
                    @if ($propertie == m){
                        margin-bottom: $bottom * $value + px !important;
                    } @else {
                        padding-bottom: $bottom * $value + px !important;
                    }
                }


                $left: nth($props, 4);
                @if ($left == 1) {
                    @if ($propertie == m){
                        margin-left: $left * $value + px !important;
                    } @else {
                        padding-left: $left * $value + px !important;
                    }
                }
            }
        }
    }
}



//**************************************************
// CUSTOM HELPERS CLASSES
//**************************************************
.list {
    @extend %list;

    &--todo {
        @include flexbox();
        @include flex-direction(row);
        @include justify-content(flex-start);
        @include flex-wrap(wrap);

        margin: 0;
        margin-left: -$gutter/2;
        margin-right: -$gutter/2;

        li {
            @include flexbox();
            @include flex(0, 0, 50%);
            @include border-box();

            padding-left: $gutter/2;
            padding-right: $gutter/2;

            margin-bottom: $gutter;

            .content {
                position: relative;
                padding: 20px 20px 20px 65px;
                margin: 0;
                background: #fff;

                &:before {
                    @extend %iconsetup;
                    content: '\ea2d';

                    position: absolute;
                    top: 25px;
                    left: 20px;
                    color: $color_01;
                    @include font-size(22);
                }
            }
        }
    }
}

hr {
    position: relative;
    width: 100%;
    height: 1px;
    background-color: $color_04;
    border: none;
    margin: 30px 0;
}

.parallax {
    width: 100%;
}


// Cookies 
.cc-window {
    z-index: 999999 !important;
}

.cc-message {
    padding-right: 50px;
}



.loader {
    &:after {
        content: " ";
        display: block;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        border: 4px solid $color_01;
        margin: 30px auto;
        border-color: $color_01 transparent $color_01 transparent;
        animation: loader-ring 1.2s linear infinite;
    }
}

@keyframes loader-ring {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}