$m: card;

.#{$m} {
	
	position: relative;
    @include border-box();
    @include transition();

    &__figure, &__content {
    	position: relative;
    	overflow: hidden;
    	@include border-box();
    	@include transition();
    }

    &__figure {
        img {
            @include transition();
        }

        .background-image {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-position: center center;
            background-size: cover;
            background-repeat: no-repeat;
            z-index: 0;
            @include transition();
        }
    }

    &__link {
        height: 100%;
        @include border-box();
    }
    
    

    &--01 {
        @include flexbox();
        @include flex-direction(column);
        @include justify-content(center);
        
        color: #000;

        &:hover {
            text-decoration: none;

            .#{$m} {
               &__figure {
                    &:before {
                        opacity: 0.6;
                    }

                    &_background {
                        @include scale(1.05);
                    }
               }
            }
        }

        .#{$m} {
            &__figure {
                position: relative;
                border-radius: 10px;
                overflow: hidden;
                height: 350px;
                border: 1px solid $color_01;
                -webkit-mask-image: -webkit-radial-gradient(white, black);

                &:before {
                    content: '';
                    position: absolute;
                    top: 0;
                    right: 0;
                    bottom: 0;
                    left: 0;
                    opacity: 0;
                    z-index: 1;
                    @include transition();
                    @include gradient-bg();
                }

                &_background {
                    position: absolute;
                    top: 0;
                    right: 0;
                    bottom: 0;
                    left: 0;
                    background-position: bottom center;
                    background-repeat: no-repeat;
                    background-size: cover;
                    @include transition();
                }

                &_badge {
                    position: absolute;
                    top: 0;
                    right: 0;
                    width: 80px;
                    height: 80px;
                    padding-top: 10px;
                    padding-right: 15px;
                    color: #fff;
                    font-weight: $bold;
                    text-align: right;
                    @include font-size(20);
                    @include border-box();

                    span {
                        position: relative;
                    }
                    
                    &:before {
                        content: '';
                        position: absolute;
                        top: 0;
                        right: 0;
                        border-top: 40px solid $color_01;
                        border-right: 40px solid $color_01;
                        border-bottom: 40px solid transparent;
                        border-left: 40px solid transparent;
                    }
                }
            }

            &__content {
                .heading {
                    margin: 15px 0;
                    @include font-size(16);
                }

                .price {
                    @include flexbox();
                    @include flex-direction(row);

                    &__col {
                        @include flexbox();
                        @include flex-direction(column);

                        margin-right: 20px;
                        min-width: 100px;

                        &:nth-child(2) {
                            color: $color_01;
                        }
                    }

                    &__current {
                        font-weight: $bold;
                        @include font-size(20);
                    }

                    &__old {
                        color: #949494;
                        @include font-size(16);   
                    }
                }
            }
        }
    }

    
    &--02 {
        @include flexbox();
        @include flex-direction(column);
        
        min-height: 260px;
        border-radius: 15px;
        overflow: hidden;
        @include gradient-bg();

        .#{$m} {

            &__content {
                padding: 30px;
                text-align: center;
                margin-top: 30px;

                .icon {
                    color: $color_01;
                    @include font-size(66);
                }

                .heading {
                    color: #fff;
                    margin: 30px 0 0;
                    @include font-size(20);
                }
            }
        }
    }
    

    &--03 {
        @include flexbox();
        @include flex-direction(row);
        @include justify-content(flex-start);

        .#{$m} {
            &__figure {
                @include flex(0, 0, 160px);

                height: 215px;
                overflow: hidden;
                border-radius: 10px;
                background-position: bottom center;
                background-repeat: no-repeat;
                background-size: cover;
                @include transition();

                @include max-screen($md - 1) {
                    @include flex(0, 0, 140px);
                }
            }

            &__content {
                @include flexbox();
                @include flex-direction(column);
                @include flex(1, 1, auto);
                
                padding-top: 10px;
                padding-bottom: 10px;
                padding-right: 10px;
                padding-left: $gutter;
            }

            &__remove_btn {
                position: absolute;
                top: -5px;
                right: -2px;
                width: 38px;
                height: 38px;
                line-height: 38px;
                text-align: center;
                color: #000;
                z-index: 10;
                @include font-size(16);
                @include transition();

                &:hover {
                    text-decoration: none;
                    color: $color_01;
                }

                &:before {
                    @extend %iconsetup;
                    content: '\ea0f';
                }
            }

            &__meta {
                .heading {
                    color: $color_01;
                    margin-bottom: 15px;
                }
            }

            &__totals {
                @include flexbox();
                @include flex-direction(row);
                @include align-items(flex-end);

                margin-top: auto;

                @include max-screen($sm - 1) {
                    @include flex-direction(column);
                }

                .qty {
                    @include max-screen($sm - 1) {
                        width: 100%;
                    }

                    .form {
                        width: 100px;

                        @include max-screen($sm - 1) {
                            width: 100%;
                            margin-top: 20px;
                        }

                        &__input {
                            &:focus {
                                -webkit-box-shadow: none;
                                -moz-box-shadow: none;
                                box-shadow: none;
                            }
                        }
                    }
                }

                .price {
                    margin-left: auto;
                    text-align: right;

                    @include max-screen($sm - 1) {
                        width: 100%;
                        text-align: left;
                        margin-top: 20px;
                    }

                    &__single {
                        margin-bottom: 5px;
                        @include font-size(14);
                    }

                    &__total {
                        margin-bottom: 0;
                        color: #000;

                        span {
                            margin-left: 20px;

                            &:nth-child(2) {
                                color: $color_01;
                            }
                        }
                    }
                }
            }
        }

        &-simple {
            .#{$m} {
                &__content {
                    @include flex-direction(row);
                    @include align-items(flex-start);

                    padding-left: 10px;
                }

                &__meta {
                    @include flex(1, 1, auto);
                    
                    .heading {

                    }
                }

                &__totals {
                    @include flex(0, 0, 250px);

                    margin-top: 0;
                }
            }
        }

    }

}
