$m: cart;

.#{$m} {
	
	position: relative;

	&__list {
		position: relative;

		&_item {
			width: 100%;
			padding: $gutter/2;
			border: 1px solid $color_04;
			border-radius: 10px;
			margin-bottom: $gutter/2;
			@include transition();
			@include border-box();

			&:hover {
				border-color: $color_01;
			}
		}
	}


	&__totals {
		margin-top: 50px;

		.total {
			@extend h3;

			span {
				margin-left: 20px;

				&:nth-child(2) {
					color: $color_01;
				}
			}
		}
	}

	&--order {
		margin: 30px 0 50px;

		.#{$m} {
			&__list {
				&_item {
					margin: 0;
					padding: 0;
					border: none;
					border-radius: 0;
					border-bottom: 1px solid $color_04;

					.meta {
						@include flexbox();
						@include flex-direction(row);
						@include justify-content(space-between);
						@include align-items(center);
						
						padding: 10px;

						> * {
							margin: 0;
						}

						.shipping {
						    span {
						        margin-left: 20px;

						        &:nth-child(2) {
						            color: $color_01;
						        }
						    }
						}

						.total {
							@extend h5;
							margin: 0;
							
						    span {
						        margin-left: 20px;

						        &:nth-child(2) {
						            color: $color_01;
						        }
						    }
						}
					}
				}
			}
		}
	}
}
