$m: wysiwyg;

.#{$m} {

    position: relative;
    
    .quote, .documents {
        margin-bottom: 30px;
    }

    h1, h2, h3, h4, h5, h6 {
        margin-top: 50px;

        &:first-child {
            margin-top: 0;
        }
    }
    
    img {
        position: relative;
        max-width: 100%;
    }
    
    ul {
        @extend %list;
		margin: 30px 0;
        padding-left: 40px;

        li {
            position: relative;
            padding: 5px 0;
            @include font-size(18);

            &:before {
                content: '';
                position: absolute;
                top: 17px;
                left: 0;
                width: 5px;
                height: 5px;
                border-radius: 50%;
                background: $color_01;
                @include translateX(-35px);
            }

            ul {
                list-style: circle;
                margin: 20px 0;
                padding-left: 30px;
                
                li {
                    padding-left: 10px;
                    @include font-size(16);

                    &:before {
                        display: none;
                    }
                }
            }
        }
    }

    ol {
        margin: 30px 0;
        padding: 0;
        list-style: none;
        counter-reset: item;

        li {
            counter-increment: item;
            position: relative;
            padding-left: 70px;
            margin: 30px 0;
            
            &:before {
                content: counter(item);
                position: absolute;
                left: 0;
                top: 0;
                width: 40px;
                text-align: center;
                color: $color_01;
                font-weight: $bold;
                padding: 2px 0;
                border-bottom: 1px solid $color_04;
            }
        }
    }
}