$m: products;

.#{$m} {
	
	position: relative;

	&__list {
		@include flexbox();
		@include flex-direction(row);
		@include justify-content(flex-start);
		@include flex-wrap(wrap);

		margin-left: -$gutter/2;
		margin-right: -$gutter/2;

		&_item {
			@include flex(0, 1, 25%);
			@include border-box();

			@include max-screen($lg - 1) {
				@include flex(0, 1, 33.33333%);
			}

			@include max-screen($md - 1) {
				@include flex(0, 1, 50%);
			}

			@include max-screen($sm - 1) {
				@include flex(1, 1, 100%);
			}

			padding: $gutter/2;
			overflow: hidden;

			@at-root .section__main #{&} {
				@include flex(0, 1, 33.33333%);

				@include max-screen(1159px) {
					@include flex(0, 1, 50%);
				}

				@include max-screen($lg - 1) {
					@include flex(0, 1, 33.33333%);
				}

				@include max-screen($md - 1) {
					@include flex(0, 1, 50%);
				}

				@include max-screen($sm - 1) {
					@include flex(1, 1, 100%);
				}
			}
		}
	}


	&__single {
		@include flexbox();
		@include flex-direction(row);
		@include justify-content(flex-start);

		@include max-screen($md - 1) {
			@include flex-wrap(wrap);
		}

		.slider {
			@include flex(0, 1, 50%);
			@include border-box();
			
			overflow: hidden;
			padding-left: $gutter/2;
			padding-right: $gutter/2;

			border-radius: 10px;
			border: 1px solid $color_01;

			@include max-screen($md - 1) {
				@include flex(0, 1, 100%);
			}

			&__main {
				.slide {
					&__figure {
						@include flexbox();
						@include justify-content(center);

						overflow: hidden;
						text-align: center;
					}
				}

				.slick {
					&-arrow {
						position: absolute;
						top: 50%;
						width: 40px;
						height: 40px;
						background: none;
						border: none;
						margin: -20px 0 0;
						padding: 0;
						z-index: 10;
						color: $color_01;
						cursor: pointer;
						@include transition();
						@include font-size(40);

						i {
							vertical-align: top;
						}

						&:hover {
							color: $color_02;
						}
					}

					&-next {
						right: 0;
					}

					&-prev {
						left: 0;
					}
				}
			}

			
		}

		.info {
			@include flex(0, 1, 50%);
			@include border-box();
			
			max-width: 600px;
			margin-left: auto;
			padding-left: $gutter/2;
			padding-right: $gutter/2;

			@include max-screen($md - 1) {
				@include flex(0, 1, 100%);

				max-width: 100%;
				margin-top: 40px;
			}

			.name {
				font-weight: $normal;
				margin-bottom: 10px;
				@include font-size(36);
			}

			.price {
				@include flexbox();
				@include flex-direction(row);

				margin: 40px 0 0;

				&__col {
				    @include flexbox();
				    @include flex-direction(column);

				    margin-right: 20px;
				    min-width: 200px;

				    &:nth-child(2) {
				        color: $color_01;
				    }
				}

				&__current {
					line-height: 1;
				    @include font-size(40);
				}

				&__old {
				    color: #949494;
				    @include font-size(20);   
				}
			}


			.form {
				@include flexbox();
				@include flex-direction(row);
				@include justify-content(flex-start);
				@include flex-wrap(wrap);

				margin-left: -$gutter/2;
				margin-right: -$gutter/2;

				&__group {
					max-width: 280px;
					padding-left: $gutter/2;
					padding-right: $gutter/2;
					padding-bottom: $gutter/2;

					@include border-box();


					.btn {
						margin-top: 25px;
					}
				}

				&__input {
					&:focus {
						-webkit-box-shadow: none;
						-moz-box-shadow: none;
						box-shadow: none;
					}
				}
			}
		}


		&_description {
			margin-top: 50px;

			.heading {
				padding-bottom: 20px;
				border-bottom: 1px solid $color_05;
			}
		}
	}
	
}
