$m: hero;

.#{$m} {
	
	position: relative;
	height: 1px;
	padding: 0 0 38% 0;

	&__slider {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		visibility: hidden;

		div {
			height: 100%;
		}


		&.slick-initialized {
			visibility: visible;
		}
		
		.slide {
			@include flexbox();
			@include flex-direction(row);
			@include align-items(center);
			
			position: relative;
			height: 100%;


			&__background {
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				background-position: center center;
				background-size: cover;
				background-repeat: no-repeat;

				&.have_mobile {
					@include max-screen($sm - 1) {
						display: none;
					}
				}

				&.mobile {
					display: none;

					@include max-screen($sm - 1) {
						display: block;
					}
				}
			}

			&__content {
				position: relative;
				width: 100%;
				height: auto;
				
				.heading {
					font-weight: $normal;

					@include max-screen($xlg - 1) {
					    @include font-size(50);
					}

					@include max-screen($lg - 1) {
					    @include font-size(40);
					}

					@include max-screen($md - 1) {
					    @include font-size(30);
					}
				}
			}
		}
	}
	
}
