$m: btn;

.#{$m} {
    @include inline-flex();
    @include flex-direction(row);
    @include justify-content(center);
    @include align-items(center);
	
	position: relative;
    min-width: 210px;
    margin: 0 0 10px 0;
    padding: 22px 25px;
    color: #fff;
    background: $color_01;
    line-height: 1.2;
    border: none;
    outline: none;
    border-radius: 30px;
    font-weight: $bold;
    font-family: $font-primary;
    @include font-size(14);
    @include border-box();
    @include transition();
    @include gradient-bg();

    &:hover {
        text-decoration: none;
        color: #000;
    }

    &:not(:disabled) {
        cursor: pointer;
    }


    &--black { 
        background: #000;

        &:hover {
            color: #000;
            background: $color_02;
        }
    }

    &--outline {
        color: #000;
        border: 1px solid $color_01;
        background: none;

        &:hover {
            color: $color_01;
        }
    }

    &--icon {
        @include justify-content(space-between);

        min-width: 280px;

        @include max-screen($sm - 1) {
            min-width: 260px;
        }
    }

    &--block {
        width: 100%;
        margin-right: 0;
    }

}



$m: link;

.#{$m} {
    @include inline-flex();
    @include flex-direction(row);
    @include align-items(center);

    color: #000;
    @include font-size(16);
    @include transition();

    &:hover {
        text-decoration: none;
        color: $color_01;
    }

    i {
        margin-left: 15px;
    }

    &--white {
        color: #fff;
    }
}
