$m: footer;

.#{$m} {
	&__inner {
		@include flexbox();
		@include flex-direction(row);
		@include justify-content(flex-start);
		@include flex-wrap(wrap);
		@include border-box();

		padding: 45px 0 20px;
		border-top: 1px solid $color_04;
		margin-left: -$gutter/2;
		margin-right: -$gutter/2;
	}

	&__col {
		@include flex(1, 1, 25%);
		@include border-box();

		padding: $gutter/2;

		@include max-screen($md - 1) {
		    @include flex(0, 0, 100%);

		    text-align: center;
		}

		&:nth-child(2) {
			@include flex(0, 0, 50%);

			@include max-screen($lg - 1) {
			    @include flex(0, 0, 60%);
			}

			@include max-screen($md - 1) {
			    @include flex(0, 0, 100%);
			}
		}

		&:nth-child(3) {
			@include max-screen($lg - 1) {
			    text-align: center;
			}
		}

		.email {
			margin-top: 20px;
			@include font-size(15);
		}

		.socials {
			&__item {
				@include inline-flex();
				@include justify-content(center);
				@include align-items(center);

				margin-right: 3px;
				width: 50px;
				height: 50px;
				background: $color_01;
				color: #fff;
				border-radius: 50%;
				@include transition();
				@include font-size(28);

				&:hover {
					text-decoration: none;
					opacity: 0.8;
				}
			}
		}
	}

	&__logo {
		text-align: left;

		img {
			height: 60px;
		}
	}

	&__menu {
		@extend %list;

		@include flexbox();
		@include flex-direction(row);
		@include justify-content(flex-start);
		@include flex-wrap(wrap);

		li {
			width: 33.3333%;
			margin-bottom: 15px;

			@include max-screen($sm - 1) {
				width: 50%;
			}

			a {
				color: #000;
				@include font-size(14);
				@include transition();

				&:hover {
					text-decoration: none;
					color: $color_01;
				}
			}
		}
	}

	&__bottom {
		background: $color_05;
		padding: 30px 0;
		@include font-size(13);
	}

	&__copyright {
		@include flexbox();
		@include flex-direction(row);
		@include justify-content(space-between);
		@include align-items(center);
		@include border-box();

		@include max-screen($md - 1) {
		    @include flex-direction(column);

		    div {
		    	margin: 10px 0;
		    }
		}

		a {
			color: #000;
		}
	}
}