$m: form;

.#{$m} {

	position: relative;
	@include clearfix();

    &__fieldset {
        padding: 50px;
        border: 1px solid $color_05;
        border-radius: 10px;
        @include border-box();

        @include max-screen($sm - 1) {
            padding: 30px 15px;
        }
    }
	
	&__group {
		position: relative;
        padding-bottom: $gutter;

        &.error {
            color: #C82D2D;
            
            .selectric {
                -webkit-box-shadow: 0px 0px 5px 1px rgba(200,45,45, 1);
                -moz-box-shadow: 0px 0px 5px 1px rgba(200,45,45, 1);
                box-shadow: 0px 0px 5px 1px rgba(200,45,45, 1);
            }
        }
	}

	&__input {
		position: relative;
		width: 100%;
		padding: 14px 25px;
        line-height: 30px;
		background: #fff;
		border: 1px solid $color_01;
		outline: none;
		color: #000;
        border-radius: 30px;
		vertical-align: bottom;
        font-family: $font-primary;
		@include border-box();
		@include font-size(16);
		@include transition();

		@at-root textarea#{&} {
			resize: none;
		}

        @at-root select#{&} {
            height: 60px;
            padding-right: 0;
        }

		&:focus {
            border-color: #000;
		}

        &.error {
            border-color: #C82D2D;
        }
	}

	&__label {
		position: relative;
		display: block;
		width: 100%;
        line-height: 20px;
		margin-bottom: 5px;
		color: #000;
		@include font-size(14);
	}
}



// Styling for custom checkboxes & radio buttons inside forms
.#{$m} {
    &__checkbox, &__radio {

		display: inline-block;	
		position: relative;
        padding: 0 0 0 35px;
        cursor: pointer;
        line-height: 20px;
        margin-bottom: 15px;
        -webkit-tap-highlight-color: transparent;

        &:last-of-type {
            margin-right: 1rem;
        }

        i {
        	position: absolute;
        	top: 0;
        	left: 0;
        	height: 18px;
        	width: 18px;
        	line-height: 18px;
        	background: #fff;
        	border: 1px solid $color_01;
            font-style: normal;
            color: $color_01;
            text-align: center;
            border-radius: 4px;
            @include font-size(11);
        }

        span {
            line-height: 16px;
            @include font-size(14);
        }
    
        [type="radio"], [type="checkbox"] {
            position: absolute;
            display: inline-block;
            opacity: 0;
        }

        [type="radio"] + i:before, [type="checkbox"] + i:before {
            @extend %iconsetup;
            line-height: 18px;
        }

        [type="radio"] {
        	+ i {
				border-radius: 50%;
        	}

            + i:before {
                content: "";
            }

            &:checked + i {
                border-color: $color_01;

                &:before {
                    content: '';
                    position: absolute;
                    top: 5px;
                    left: 5px;
                    right: 5px;
                    bottom: 5px;
                    border-radius: 50%;
                    background: $color_01;
                }

                + span {
                    // font-weight: $bold;
                }
            }
        }

        [type="checkbox"] {
            + i:before {
                content: "";
            }

            &:checked + i {
            	background: $color_01;

                &:before {
                    content: "\ea10";
                    color: #fff;
                }

                + span {
                    //font-weight: $bold;
                }
            }
        }
    }
}