//---------------------------------------------------------------
// CORE SETTINGS
//---------------------------------------------------------------
html {
    font-size: $base-fontSize;
}

figure {
    position: relative;
    margin: 0;
    padding: 0;
    font-size: 0;
}

video {
    width: 100% !important;
    height: auto !important;
}

img {
    vertical-align: bottom;
}





//---------------------------------------------------------------
// SITE WRAPPERS & LAYOUT MODIFICATIONS
//---------------------------------------------------------------
body {
    background: #fff;
    text-align: left;
    padding-top: 80px;

    &.locked {
    	position: fixed;
	    overflow: hidden;
		height: 100%;
		width: 100%;
    }
}


.container {
	margin: 0 auto;
    width: 100%;
	@include border-box();
	@include clearfix();

    &--outer {
        max-width: 100%;
    }
}

.section {
    position: relative;
    width: 100%;
    padding: 50px 0;

    @include max-screen($md - 1) {
        padding: 30px 0;
    }

    &__heading {
        text-align: center;
        margin-bottom: 50px;

        span {
            position: relative;
            display: inline-block;
            padding: 0 80px;

            &:before, &:after {
                content: '';
                position: absolute;
                top: 50%;
                left: 0;
                width: 45px;
                height: 1px;
                background: $color_01;
            }

            &:after {
                left: auto;
                right: 0;
            }
        }
    }


    &--no-gutter {
        padding: 0;

        &-bottom {
            padding-bottom: 0;
        }
    }

    &--gutter-small {
        padding: 30px 0;
    }

    &--gutter-large {
        padding: 100px 0;
    }

    
  

    &--image {
        @include flexbox();
        @include flex-direction(row);
        @include justify-content(flex-end);
        @include align-items(flex-end);

        text-align: center;
        height: 560px;

        .content {
            float: right;
            width: 370px;
            color: #fff;
            text-align: left;

            &--left {
                float: left;
            }

            .heading {
                line-height: 1.4;
            }
        }
    }
    

    &__background {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-position: center center;
        background-size: cover;
        background-repeat: no-repeat;

        z-index: -1;
    }

    &__inner {
        @include flexbox();
        @include flex-direction(row);
        @include justify-content(flex-start);

        margin-left: -$gutter/2;
        margin-right: -$gutter/2;

        @include max-screen($lg - 1) {
            @include flex-wrap(wrap);
        }
    }
}