$m: header;

.#{$m} {

	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 80px;
	z-index: 100;
	background: #fff;
	box-shadow: 0 0 3px #000;
	
	@include transition();

	@include max-screen($md - 1) {
	}

	
	&__inner {
		@include flexbox();
		@include flex-direction(row);
		@include justify-content(space-between);
		@include align-items(center);
		@include border-box();
		
		height: 80px;
		@include transition();
	}


	&__logo {
		min-width: 210px;

		@include max-screen($lg - 1) {
			min-width: 100px;
		}

		img {
			@include transition();
			height: 40px;

			@include max-screen($sm - 1) {
			    height: 30px !important;
			}
		}
	}

	&__actions {
		@include flexbox();
		@include justify-content(flex-end);
		@include align-items(center);

		font-weight: $bold;
		min-width: 250px;
		@include font-size(14);

		@include max-screen($lg - 1) {
			margin-left: auto;
			min-width: 20px;
		}

		a {
			display: inline-block;
			color: #000;
			@include transition();

			&:hover {
				text-decoration: none;
				color: $color_01;
			}
		}
		
		.cart {
			padding: 0 25px;
			height: 58px;
			line-height: 58px;

			@include max-screen($lg - 1) {
			    height: 40px;
			    line-height: 40px;
			    padding: 0 15px;
			}

			.icon {
				@include font-size(15);
			}

			&__items {
				span {
					margin-left: 5px;

					@include max-screen($xlg - 1) {
						display: none;
					}
				}
			}

			&__total {
				color: $color_01;
				margin-left: 15px;

				@include max-screen($xlg - 1) {
					display: none;
				}
			}
		}
	}

	&__hamburger {
		position: relative;
		width: 60px;
		height: 60px;
		border: none;
		outline: none;
		padding: 0;
		background: none;
		text-indent: -999px;
		overflow: hidden;
		z-index: 5;
		cursor: pointer;
		margin: 0;
		display: none;
		@include transition();

		@include max-screen($lg - 1) {
			display: block;
			margin-left: 10px;
		}

		
		span {
			display: block;
			position: absolute;
			top: 50%;
			margin-top: -1px;
			left: 15px;
			right: 15px;
			height: 2px;
			background: #000;
			@include transition();
			
			&:before, &:after {
                content: " ";
                position: absolute;
                display: block;
                left: 0;
				right: 0;
                height: 2px;
                background-color: #000;
                -webkit-transition-duration: 0.3s, 0.3s;
                transition-duration: 0.3s, 0.3s;
                -webkit-transition-delay: 0.3s, 0s;
                transition-delay: 0.3s, 0s;
            }

            &:before {
                top: -10px;
                -webkit-transition-property: top, -webkit-transform;
                transition-property: top, transform;
            }
            &:after {
                bottom: -10px;
                -webkit-transition-property: bottom, -webkit-transform;
                transition-property: bottom, transform;
            }
		}
		
		&.is_open {
            span {
                background: none;

                &:before {
                    top: 0;
                    @include rotate(45deg);
                }

                &:after {
                    bottom: 0;
                    @include rotate(-45deg);
                }

                &:before, &:after {
                    -webkit-transition-delay: 0s, 0.3s;
                    transition-delay: 0s, 0.3s;

                    @include max-screen($md - 1) {
                    	background-color: #000;
                    }
                }
            }
        }
	}


	&__menu {
		@include max-screen($lg - 1) {
			position: absolute;
			top: 80px;
			left: 0;
			width: 100%;
			height: calc(100vh - 80px);
			background: $color_02;
			@include transition();
			@include translateX(-100%);

			&.is_open {
				overflow-y: auto;
				@include translateX(0);				
			}
		}
	}

}



$m: menu;

.#{$m} {
	@include flexbox();
	@include flex-direction(row);
	@include justify-content(flex-start);
	@include border-box();
	
	@extend %list;
	width: 100%;
	text-align: center;

	@include max-screen($lg - 1) {
		@include flex-direction(column);

		padding: 0 0 20px;
		text-align: left;
	}
	
	&__item {
		position: relative;
		padding: 0 25px;

		-webkit-tap-highlight-color: transparent;
		-webkit-touch-callout: none;
		-webkit-user-select: none;
		-khtml-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;

		@include max-screen($lg - 1) {
			border-bottom: 1px solid $color_01;
			padding: 0;
		}

		&--actions {
			@include flexbox();
			@include justify-content(flex-start);
			@include align-items(center);

			font-weight: $bold;
			@include font-size(14);


			@include min-screen($md) {
				display: none;
			}

			.cart {
				width: 50%;
				@include border-box();

				&__link {
					display: block;
					color: #fff;
					text-align: center;
					padding: 20px;
					text-decoration: none;
				}

				.icon {
					margin-right: 5px;
					@include font-size(15);
				}

				&__total {
					color: $color_01;
					margin-left: 15px;
				}
			}

			.item {
				width: 50%;
				display: inline-block;
				height: 63px;
				color: #000;
				border-left: 1px solid #333;
				@include border-box();

				&:only-child {
					width: 100%;
				}

				&__link {
					display: block;
					color: #fff;
					padding:20px;
					text-align: center;
					text-decoration: none;
				}

				.icon {
					margin-right: 5px;
					@include font-size(15);
				}
			}
		}
	}


	&__link {
		position: relative;
		
		color: #000;
		font-weight: $bold;
		@include font-size(14);
		@include transition();

		@include max-screen($lg - 1) {
			display: block;
			padding: 15px 20px;
			color: $color_01;
			@include font-size(20);
		}

		&:hover {
			color: $color_01;
			text-decoration: none;
		}
	}
}
