$m: delivery;

.#{$m} {
	
	position: relative;

	&--dark-bg {
		padding: 15px 0;
		background: $color_01;
		color: #fff;
	}

	&__ribbon {
		@include flexbox();
		@include flex-direction(row);
		@include justify-content(center);
		@include align-items(center);
		@include border-box();
		
		&_content {
			line-height: 32px;
			text-align: center;
		}

		&_button {
			margin-left: auto;

			.link {
				@include font-size(14);
			}
		}
	}

	&__cards {
		@include flexbox();
		@include flex-direction(row);
		@include justify-content(flex-start);
		@include flex-wrap(wrap);

		margin-left: -$gutter/2;
		margin-right: -$gutter/2;

		&_item {
			@include flex(0, 1, 33.3333%);
			padding: $gutter/2;

			@include border-box();

			@include max-screen($lg - 1) {
			    @include flex(1, 1, 50%);
			}

			@include max-screen($md - 1) {
			    @include flex(1, 1, 100%);

			    padding-top: 0;
			}
		}
	}
	
}
